.main-hero {
    background: $color1;
    height: calc(100vh + 50px);
    position: relative;
    overflow: hidden;

    display: flex;
    align-items: center;
    justify-content: center;
}
.main-hero-video {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width :100%;
}
.main-hero-video video {
    object-fit: cover;
}
.main-hero-video:before {
    content:'';
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background: linear-gradient($color1, $color2);
    opacity:0.8;
}

.main-hero-footer {
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    height:50px;
    display: flex;
}
.main-hero-footer:before,
.main-hero-footer:after {
    content:'';
    background: #fff;
    flex-grow:1;
}
.main-hero-footer span {
    background: url(../images/banner-bottom.svg);
    width:260px;
    height:50px;
    display: block;
}

.main-hero-scroll {
    width:20px;
    height:42px;
    border:1px solid rgba(#fff, 0.3);
    position: absolute;
    bottom:70px;
    left:50%;
    transform:translate(-50%, 0);
    border-radius:10px;
}
.main-hero-scroll:before {
    content:'';
    width:6px;
    height:12px;
    background: #fff;
    border-radius: 3px;
    margin:6px auto;
    display: block;
    animation: mainHeroScrollAnim 0.7s 0s infinite alternate ease-in-out;
}

@keyframes mainHeroScrollAnim {
    100% {
        margin-top:22px;
    }
}